import { createSlice, SerializedError } from "@reduxjs/toolkit"
import { CategoryView } from "../../modules/admin/models/CategoryView"
import { TemplateView } from "../../modules/admin/models/TemplateView"
import * as adminActions from "./admin.actions"


export const adminFeatureKey = "adminFeatureKey"

export interface RootAdminState {
    [adminFeatureKey]: InitialState
}
export interface InitialState {
    loading: boolean,
    errorMessage: SerializedError,
    template: TemplateView,
    templates: TemplateView[],
    category: CategoryView,
    categories: CategoryView[],
    successMessage: string
}

const initialState: InitialState = {
    loading: false,
    errorMessage: {} as SerializedError,
    template: {} as TemplateView,
    templates: [] as TemplateView[],
    category: {} as CategoryView,
    categories: [] as CategoryView[],
    successMessage: ""

}

export const adminSlice = createSlice({
    name: "adminSlice",
    initialState: initialState,
    reducers: {},
    extraReducers: (builder: any) => {
        // get all Template list
        builder.addCase(adminActions.getTemplateListAction.pending, (state: any) => {
            state.loading = true;
        }).addCase(adminActions.getTemplateListAction.fulfilled, (state: any, action: any) => {
            state.loading = false;

            console.log("adminSlice called")
            state.templates = action.payload.data;
        }).addCase(adminActions.getTemplateListAction.rejected, (state: any, action: any) => {
            state.loading = false;
            state.errorMessage = action.error;
            state.successMessage = "";
            state.templates = [];

        })


        // addd Template list
        builder.addCase(adminActions.addTemplateAction.pending, (state: any) => {
            state.loading = true;
        }).addCase(adminActions.addTemplateAction.fulfilled, (state: any, action: any) => {
            state.loading = false;
            // state.successMessage = action.payload.msg

        }).addCase(adminActions.addTemplateAction.rejected, (state: any, action: any) => {
            state.loading = false;
            console.log("sdfsdfsdfsdfsd")
            console.log(action)
            state.errorMessage = action.error;
            // state.successMessage = action.payload.msg

            state.successMessage = "";

        })
           
        // Edit Template list
        builder.addCase(adminActions.editTemplateAction.pending, (state: any) => {
            state.loading = true;
        }).addCase(adminActions.editTemplateAction.fulfilled, (state: any, action: any) => {
            state.loading = false;
        }).addCase(adminActions.editTemplateAction.rejected, (state: any, action: any) => {
            state.loading = false;
            state.errorMessage = action.error;
            state.successMessage = "";

        })
           
        // get a Template 
        builder.addCase(adminActions.getTemplateAction.pending, (state: any) => {
            state.loading = true;
        }).addCase(adminActions.getTemplateAction.fulfilled, (state: any, action: any) => {
            state.loading = false;
            state.template = action.payload.data;
        }).addCase(adminActions.getTemplateAction.rejected, (state: any, action: any) => {
            state.loading = false;
            state.errorMessage = action.error;
            state.successMessage = "";
            state.template = {} as TemplateView

        })

        //================

        // get all Template list
        builder.addCase(adminActions.getCategoryListAction.pending, (state: any) => {
            state.loading = true;
        }).addCase(adminActions.getCategoryListAction.fulfilled, (state: any, action: any) => {
            state.loading = false;
            state.categories = action.payload.data;
        }).addCase(adminActions.getCategoryListAction.rejected, (state: any, action: any) => {
            state.loading = false;
            state.errorMessage = action.error;
            state.successMessage = "";
            state.categories = [];

        })


        // addd Template list
        builder.addCase(adminActions.addCategoryAction.pending, (state: any) => {
            state.loading = true;
        }).addCase(adminActions.addCategoryAction.fulfilled, (state: any, action: any) => {
            state.loading = false;
        }).addCase(adminActions.addCategoryAction.rejected, (state: any, action: any) => {
            state.loading = false;
            state.errorMessage = action.error;
            state.successMessage = "";

        })
           
        // Edit Template list
        builder.addCase(adminActions.editCategoryAction.pending, (state: any) => {
            state.loading = true;
        }).addCase(adminActions.editCategoryAction.fulfilled, (state: any, action: any) => {
            state.loading = false;
        }).addCase(adminActions.editCategoryAction.rejected, (state: any, action: any) => {
            state.loading = false;
            state.errorMessage = action.error;
            state.successMessage = "";

        })
           
        // get a Template 
        builder.addCase(adminActions.getCategoryAction.pending, (state: any) => {
            state.loading = true;
        }).addCase(adminActions.getCategoryAction.fulfilled, (state: any, action: any) => {
            state.loading = false;
            state.category = action.payload.data;
        }).addCase(adminActions.getCategoryAction.rejected, (state: any, action: any) => {
            state.loading = false;
            state.errorMessage = action.error;
            state.successMessage = "";
            state.category = {} as CategoryView;


        })
           

    }
})