import { createSlice, SerializedError } from "@reduxjs/toolkit"
import { OrderView } from "../../modules/order/models/OrderView"
import { UserView } from "../../modules/users/models/UserView"
import { AuthUtil } from "../../util/AuthUtil"
import * as orderActions from "./order.actions"


export const ordersFeatureKey = "ordersFeatureKey"

export interface RootOrderState {
    [ordersFeatureKey]: InitialState
}
export interface InitialState {
    loading: boolean,
    errorMessage: SerializedError,
    order: OrderView,
    orders: OrderView[],
    successMessage: string
}

const initialState: InitialState = {
    loading: false,
    errorMessage: {} as SerializedError,
    order: {} as OrderView,
    orders: [] as OrderView[],
    successMessage: ""

}

export const orderSlice = createSlice({
    name: "orderSlice",
    initialState: initialState,
    reducers: {},
    extraReducers: (builder: any) => {
        // add order by users
        builder.addCase(orderActions.addOrderAction.pending, (state: any) => {
            state.loading = true;
        }).addCase(orderActions.addOrderAction.fulfilled, (state: any, action: any) => {
            state.loading = false;
            state.successMessage = action.payload.msg;
        }).addCase(orderActions.addOrderAction.rejected, (state: any, action: any) => {
            state.loading = false;
            state.errorMessage = action.error;
            state.successMessage = "";
        })
            // upload the attachment by Celeb
            .addCase(orderActions.uploadAttachmentAction.pending, (state: any) => {
                state.loading = true;
            }).addCase(orderActions.uploadAttachmentAction.fulfilled, (state: any, action: any) => {
                state.loading = false;
                state.successMessage = action.payload.msg;
                state.order = action.payload.data
            }).addCase(orderActions.uploadAttachmentAction.rejected, (state: any, action: any) => {
                state.loading = false;
                state.errorMessage = action.error;
                state.successMessage = action.payload.msg;
            })

            // delete order
            .addCase(orderActions.deleteOrderAction.pending, (state: any) => {
                state.loading = true;
            }).addCase(orderActions.deleteOrderAction.fulfilled, (state: any, action: any) => {
                state.loading = false;
                state.successMessage = action.payload.msg;
            }).addCase(orderActions.deleteOrderAction.rejected, (state: any, action: any) => {
                state.loading = false;
                state.errorMessage = action.error;
                state.successMessage = "";
            })

            // get all order
            .addCase(orderActions.getAllOrdersAction.pending, (state: any) => {
                state.loading = true;
            }).addCase(orderActions.getAllOrdersAction.fulfilled, (state: any, action: any) => {
                state.loading = false;
                state.orders = action.payload.data;
            }).addCase(orderActions.getAllOrdersAction.rejected, (state: any, action: any) => {
                state.loading = false;
                state.errorMessage = action.error;
                state.successMessage = "";
                state.orders = [];
            })

             // get a order
             .addCase(orderActions.getOrderAction.pending, (state: any) => {
                state.loading = true;
            }).addCase(orderActions.getOrderAction.fulfilled, (state: any, action: any) => {
                state.loading = false;
                state.order = action.payload.data;
            }).addCase(orderActions.getOrderAction.rejected, (state: any, action: any) => {
                state.loading = false;
                state.errorMessage = action.error;
                state.successMessage = "";
                state.order = {} as OrderView;
            })

             // update Status
             .addCase(orderActions.updateStatusAction.pending, (state: any) => {
                state.loading = true;
            }).addCase(orderActions.updateStatusAction.fulfilled, (state: any, action: any) => {
                state.loading = false;
            }).addCase(orderActions.updateStatusAction.rejected, (state: any, action: any) => {
                state.loading = false;
                state.errorMessage = action.error;
                state.successMessage = "";
            }) 


    }
})