import React, { useEffect, useState } from 'react';
import './App.css';
import './App.css';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import NavBarSocial from './modules/layout/components/navbar/NavBarSocial';
import Home from './modules/layout/components/home/Home';
import UserLogin from './modules/users/components/login/UserLogin';
import UserRegister from './modules/users/components/register/UserRegister';
import CelebList from './modules/users/components/dashboard/CelebList';
import MyOrderList from './modules/users/components/dashboard/MyOrderList';
import About from './modules/layout/components/about/About';
import AddOrder from './modules/users/components/dashboard/AddOrder';
import PrivateRoute from './router/PrivateRoute';
import { AppDispatch } from './redux/store';
import { useDispatch } from 'react-redux';
import { AuthUtil } from './util/AuthUtil';
import * as userActions from './redux/users/user.actions';
import { ToastContainer } from 'react-toastify';
import UploadOrder from './modules/users/components/dashboard/UploadOrder';
import OrderDetail from './modules/users/components/dashboard/OrderDetail';
import TemplateList from './modules/admin/components/TemplateList';
import AddTemplate from './modules/admin/components/AddTemplate';
import EditTemplate from './modules/admin/components/EditTemplate';
import AddCategory from './modules/admin/components/AddCategory';
import EditCategory from './modules/admin/components/EditCategory';
import CategoryList from './modules/admin/components/CategoryList';


function App() {

  const dispatch: AppDispatch = useDispatch();

    useEffect(() => {
        if (AuthUtil.isLoggedIn()) {
          console.log('token valid')
            dispatch(userActions.getUserInfoAction());
        }
    }, []);

  return (
    < >

      <BrowserRouter>
      <ToastContainer
                    position="top-center"
                    autoClose={2000}
                    hideProgressBar={false}
                    newestOnTop={false}
                    closeOnClick
                    rtl={false}
                    pauseOnFocusLoss
                    draggable
                    pauseOnHover
                    theme="light"
                />
        <Routes>
          <Route path={'/'} element={<Home />} />
          <Route path={'/users/register'} element={<UserRegister />} />
          <Route path={'/users/login'} element={<UserLogin />} />
          <Route path={'/celeb/list'} element={<CelebList />} />
          <Route path={'/order/me'} element={
            <PrivateRoute>
              <MyOrderList />
            </PrivateRoute>
          } />
          <Route path={'/order/add/:celebID'} element={
            <PrivateRoute>
              <AddOrder />
            </PrivateRoute>
          } />
          <Route path={'/order/upload/:orderId'} element={
            <PrivateRoute>
              <UploadOrder />
            </PrivateRoute>
          } />

<Route path={'/order/detail/:orderId'} element={
            <PrivateRoute>
              <OrderDetail />
            </PrivateRoute>
          } />

<Route path={'/templates'} element={
            <PrivateRoute>
              <TemplateList />
            </PrivateRoute>
          } />
          <Route path={'/templates/add'} element={
            <PrivateRoute>
              <AddTemplate />
            </PrivateRoute>
          } />

<Route path={'/templates/edit/:templateId'} element={
            <PrivateRoute>
              <EditTemplate ></EditTemplate>
            </PrivateRoute>
          } />
<Route path={'/categories'} element={
            <PrivateRoute>
              <CategoryList />
            </PrivateRoute>
          } />
<Route path={'/categories/add'} element={
            <PrivateRoute>
              <AddCategory />
            </PrivateRoute>
          } />

<Route path={'/categories/edit/:categoryId'} element={
            <PrivateRoute>
              <EditCategory ></EditCategory>
            </PrivateRoute>
          } />
          <Route path={'/about'} element={<About />} />

          
        </Routes>
      </BrowserRouter>
    </>
  );
}

export default App;
