import { createAsyncThunk } from "@reduxjs/toolkit";
import { AddOrderView, OrderView } from "../../modules/order/models/OrderView";
import { OrderServices } from "../../modules/order/services/OrderServices";
import { TokenUtil } from "../../util/TokenUtil";
 


// adding order by client
export const addOrderAction =  createAsyncThunk('addOrderAction', async(order: AddOrderView)=>{
     let response = await OrderServices.addOrder(order)
    return response.data
})
// upload the attachment  by Celeb
export const uploadAttachmentAction =  createAsyncThunk('uploadAttachmentAction', async(payload:{order: OrderView, orderId: string})=>{
    let {order, orderId} = payload;
    let response = await OrderServices.uploadAttachment(order,orderId)
    return response.data
})

// delete the order
export const deleteOrderAction = createAsyncThunk('deleteOrderAction', async(orderId: string)=>{
    let response = await OrderServices.deleteOrder(orderId)
    return response.data;
})

// get all the order according to token
export const getOrderAction = createAsyncThunk('getOrderAction', async(orderId:string)=>{
    console.log("getOrderAction2")

    if (TokenUtil.isSetToken()) {

        console.log("getOrderAction1")
        let response = await OrderServices.getOrder(orderId)
    return response.data;
    }
    
})


// get all the order according to token
export const getAllOrdersAction = createAsyncThunk('getAllOrdersAction', async()=>{
    if (TokenUtil.isSetToken()) {
        let response = await OrderServices.getAllOrders()
    return response.data;
    }
    
})

// update the status of order by ADMIN
export const updateStatusAction = createAsyncThunk('updateStatusAction', async(orderId: string )=>{
    let response = await OrderServices.updateStatus(orderId)
    return response.data;
})