import axios from 'axios';
import { AddOrderView, OrderView } from '../models/OrderView';

export class OrderServices {
    private static serverUrl = process.env.REACT_APP_EXPRESS_SERVER;

    private static orderAPI: string = `${this.serverUrl}/api/order`

    // private
    public static addOrder(order: AddOrderView) {
        return axios.post(`${this.orderAPI}`, order);
    }

    // Private for Celeb
    public static uploadAttachment(order: OrderView, orderId: string) {
        return axios.put(`${this.orderAPI}/upload/${orderId}`, order);
    }

    // Delete
    public static deleteOrder(orderId: string) {
        return axios.delete(`${this.orderAPI}/${orderId}`);
    }

    // get all order
    public static getAllOrders() {
        return axios.get(`${this.orderAPI}/me`);
    }

     // get a order
     public static getOrder(orderId:string) {
        return axios.get(`${this.orderAPI}/detail/${orderId}`);
    }

    // Private
    public static updateStatus( orderId: string) {
        return axios.put(`${this.orderAPI}/status/${orderId}`);
    }
}