import React from 'react';
import { Container, Nav, Navbar, NavDropdown } from "react-bootstrap";
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import { Link } from "react-router-dom";
import { AppDispatch } from '../../../../redux/store';
import { logOutAction, RootUserState, usersFeatureKey } from '../../../../redux/users/user.slice';
import { AuthUtil } from '../../../../util/AuthUtil';

interface IProps {
}

let NavBarSocial: React.FC<IProps> = ({ }) => {

    const dispatch: AppDispatch = useDispatch();

    const userState = useSelector((state: RootUserState) => {
        return state[usersFeatureKey];
    });

    const { isAuthenticated, user } = userState;

    const clickLogOut = () => {
        dispatch(logOutAction({}));
    };
    return (
        <>
            <Navbar bg="dark" expand="sm" variant="dark">
                <Container>
                    <Navbar.Brand>
                        <Link to={'/'} className="text-decoration-none text-light">Celeb</Link>
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="me-auto">

                            {
                                AuthUtil.isLoggedIn() && user && !user.isCeleb && !user.isAdmin &&
                                <>
                                    <Nav.Link>
                                        <Link to={'/celeb/list'}
                                            className="text-decoration-none text-light">Celeb List</Link>
                                    </Nav.Link>
                                    <Nav.Link>
                                        <Link to={'/order/me'} className="text-decoration-none text-light">My Order</Link>
                                    </Nav.Link>
                                </>
                            }

                            {
                                AuthUtil.isLoggedIn() && user && user.isCeleb &&
                                <>
                                    <Nav.Link>
                                        <Link to={'/order/me'} className="text-decoration-none text-light">My Order</Link>
                                    </Nav.Link>
                                </>
                            }
                            {
                                AuthUtil.isLoggedIn() && user && user.isAdmin &&
                                <>
                                    <Nav.Link>
                                        <Link to={'/order/me'} className="text-decoration-none text-light">All Order</Link>
                                    </Nav.Link>
                                    <Nav.Link>
                                        <Link to={'/celeb/list'} className="text-decoration-none text-light">Celeb</Link>
                                    </Nav.Link>
                                    <Nav.Link>
                                        <Link to={'/categories'} className="text-decoration-none text-light">Category</Link>
                                    </Nav.Link>
                                    <Nav.Link>
                                        <Link to={'/templates'} className="text-decoration-none text-light">Template</Link>
                                    </Nav.Link>
                                </>
                            }



                            <Nav.Link>
                                <Link to={'/about'} className="text-decoration-none text-light">About</Link>
                            </Nav.Link>
                        </Nav>
                        <Nav className="ms-auto">

                            {
                                AuthUtil.isLoggedIn() &&
                                <>
                                    {
                                        Object.keys(user).length > 0 &&
                                        <Nav.Link>
                                            <Link to={''}
                                                className="text-decoration-none text-light">
                                                <img src={user.imageUrl} alt="" width={20}
                                                    className="rounded-circle shadow-lg" />{' '}
                                                {user.name}</Link>
                                        </Nav.Link>
                                    }

                                    <Nav.Link>
                                        <Link to={'/'}
                                            className="text-decoration-none text-light"
                                            onClick={clickLogOut}>
                                            <i className="fa fa-sign-out text-muted"></i> LogOut</Link>
                                    </Nav.Link>
                                    
                                </>
                            }
                            {
                                !AuthUtil.isLoggedIn() &&
                                <>
                                    <Nav.Link>
                                        <Link to={'/users/login'}
                                              className="text-decoration-none text-light">
                                            <i className="fa fa-sign-in text-muted"></i> Login</Link>
                                    </Nav.Link>
                                </>

                            }
                            
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </>
    )
};
export default NavBarSocial;