import { createAsyncThunk } from "@reduxjs/toolkit"
import { CelebServices } from "../../modules/celeb/services/CelebServices"


// get all Celeb List
export const getCelebListAction =  createAsyncThunk('getCelebListAction', async()=>{
    let response = await CelebServices.getAllCeleb()
   return response.data
})

// get all Approved Celeb List
export const getCelebApprovedListAction =  createAsyncThunk('getCelebApprovedListAction', async()=>{
    let response = await CelebServices.getAllApprovedCeleb()
   return response.data
})