import axios from 'axios';
import { CategoryView } from '../models/CategoryView';
import { TemplateView } from '../models/TemplateView';

export class AdminServices {
    private static serverUrl = process.env.REACT_APP_EXPRESS_SERVER;

    private static templateAPI: string = `${this.serverUrl}/api/template`
    private static categoryAPI: string = `${this.serverUrl}/api/category`

    // private template
    public static addTemplate(template: TemplateView) {
        return axios.post(`${this.templateAPI}`, template);
    }

    // Private for template
    public static editTemplate(template: TemplateView, templateId: string) {
        return axios.put(`${this.templateAPI}/${templateId}`, template);
    }
 

    // get all template
    public static getAllTemplate() {
        return axios.get(`${this.templateAPI}`);
    }

    public static getTemplate(templateId:string) {
        return axios.get(`${this.templateAPI}/${templateId}`);
    }

     // private Category
     public static addCategory(category: CategoryView) {
        return axios.post(`${this.categoryAPI}`, category);
    }

    // Private for category
    public static editCategory(category: CategoryView, categoryId: string) {
        return axios.put(`${this.categoryAPI}/${categoryId}`, category);
    }
 

    // get all category
    public static getAllCategory() {
        return axios.get(`${this.categoryAPI}`);
    }

    public static getCategory(categoryId:string) {
        return axios.get(`${this.categoryAPI}/${categoryId}`);
    }
    

  }