import React from 'react';
import {Button} from "react-bootstrap";
import {Link} from "react-router-dom";

interface IProps{}

let Home:React.FC<IProps> = ({}) => {
    return (
        <>
            <div className="landing">
                <div className="wrapper">
                    <div className="d-flex flex-column justify-content-center align-items-center h-100 text-center">
                        <h3 className="display-2">Choose Celeb</h3>
                        <p className="lead">Choose your celb and make video or photo for your relative to wish them</p>
                        <div>
                            
                            <Button variant={'success'}>
                                <Link to={'/users/login'} className="text-decoration-none text-white">Login</Link>
                            </Button>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
};
export default Home;