import React, { useEffect, useState } from 'react';
import { Button, Card, Col, Container, Form, InputGroup, Row, Table } from "react-bootstrap";
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { celebsFeatureKey, RootCelebState } from '../../../../redux/celeb/celeb.slice';
import { AppDispatch } from '../../../../redux/store';
import { RootUserState, usersFeatureKey } from '../../../../redux/users/user.slice';
import NavBarSocial from '../../../layout/components/navbar/NavBarSocial';
import * as celebActions from '../../../../redux/celeb/celeb.actions'
import CelebCard from '../celeb-card/CelebCard';
import Spinner from '../../../layout/components/spinner/Spinner';
import * as userActions from '../../../../redux/users/user.actions'
  
interface IProps { }

let CelebList: React.FC<IProps> = ({ }) => {
 
  const [counter, setCounter] = useState(0);

  const setApproved = (celebId: string) => {
    if (celebId) {
        dispatch(userActions.updateCelebStatusAction(celebId)).then(() => {
          setCounter(counter+1)
          // dispatch(celebActions.getCelebListAction());
        }).catch(() => {
          setCounter(counter+1)
          // dispatch(celebActions.getCelebListAction());
        })
  
      }
  };

  const celebState = useSelector((state: RootCelebState) => {
    return state[celebsFeatureKey];
  })

  const userState = useSelector((state: RootUserState) => {
    return state[usersFeatureKey]
  })

  let { loading, errorMessage, successMessage, celebs } = celebState;
  let { user } = userState;
  const dispatch: AppDispatch = useDispatch();
  useEffect(() => {
    if (user.isAdmin) {
      dispatch(celebActions.getCelebListAction());
    }else{
      dispatch(celebActions.getCelebApprovedListAction());

    }  }, [])
  useEffect(() => {
    console.log("this is reload")
    if (user.isAdmin) {
      dispatch(celebActions.getCelebListAction());
    }else{
      dispatch(celebActions.getCelebApprovedListAction());

    }
  }, [counter])
  return (
    <>
      <NavBarSocial />
      {
             loading && <Spinner/>
            }
      <Container className="mt-3">
        <Row>
          <Col>
            <h3 className="text-success">
            {/* <i className="fa fa-facebook-official"></i> Celebraties */}
            <i className="fa fa-group"></i> Celebraties
            </h3>
            <p className="fst-italic">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Accusantium
              ad adipisci, assumenda atque deleniti deserunt dolorem doloribus maxime neque, odit placeat
              quas quibusdam quidem quis quisquam quos reprehenderit similique ut!</p>
          </Col>
        </Row>
      </Container>



      <Container>
        <Row>

          {
            celebs.map((celebObj, index) => {
              return (
                <>
                  <Col xs="3" className='mt-3' key={index}>
                    <CelebCard  setApprove={setApproved}  celeb={celebObj} isUser={!user.isAdmin && !user.isCeleb} isAdmin={user.isAdmin}></CelebCard>
                  </Col>
                </>
              )
            }
            )
          }
        </Row>
      </Container>
    </>
  )
};
export default CelebList;