import { createAsyncThunk } from "@reduxjs/toolkit"
import { CategoryView } from "../../modules/admin/models/CategoryView"
import { TemplateView } from "../../modules/admin/models/TemplateView"
import { AdminServices } from "../../modules/admin/services/AdminServices"


// get all Template List
export const getTemplateListAction =  createAsyncThunk('getTemplateListAction', async()=>{
    let response = await AdminServices.getAllTemplate()
   return response.data
})

// add  Template List
export const addTemplateAction =  createAsyncThunk('addTemplateAction', async(template: TemplateView)=>{
    let response = await AdminServices.addTemplate(template)
    console.log('Bhavesh')

    console.log(`response : ${response}`)
   return response.data
})

export const editTemplateAction =  createAsyncThunk('editTemplateAction', async(payload:{template: TemplateView, templateId: string})=>{
    let {template, templateId} = payload
    let response = await AdminServices.editTemplate(template,templateId)
   return response.data
})

 

export const getTemplateAction =  createAsyncThunk('getTemplateAction', async(templateId: string)=>{
    
    let response = await AdminServices.getTemplate(templateId)
   return response.data
})



