import { createSlice, SerializedError } from "@reduxjs/toolkit"
import { UserView } from "../../modules/users/models/UserView"
import * as celebActions from "./celeb.actions"


export const celebsFeatureKey = "celebsFeatureKey"

export interface RootCelebState {
    [celebsFeatureKey]: InitialState
}
export interface InitialState {
    loading: boolean,
    errorMessage: SerializedError,
    celeb: UserView,
    celebs: UserView[],
    successMessage: string
}

const initialState: InitialState = {
    loading: false,
    errorMessage: {} as SerializedError,
    celeb: {} as UserView,
    celebs: [] as UserView[],
    successMessage: ""

}

export const celebSlice = createSlice({
    name: "celebSlice",
    initialState: initialState,
    reducers: {},
    extraReducers: (builder: any) => {
        // get all celeb list
        builder.addCase(celebActions.getCelebListAction.pending, (state: any) => {
            state.loading = true;
        }).addCase(celebActions.getCelebListAction.fulfilled, (state: any, action: any) => {
            state.loading = false;
            state.celebs = action.payload.celebs;
        }).addCase(celebActions.getCelebListAction.rejected, (state: any, action: any) => {
            state.loading = false;
            state.errorMessage = action.error;
            state.successMessage = "";
            state.celebs = [];

        })

        // get all approved celeb list
        .addCase(celebActions.getCelebApprovedListAction.pending, (state: any) => {
            state.loading = true;
        }).addCase(celebActions.getCelebApprovedListAction.fulfilled, (state: any, action: any) => {
            state.loading = false;
            state.celebs = action.payload.celebs;
        }).addCase(celebActions.getCelebApprovedListAction.rejected, (state: any, action: any) => {
            state.loading = false;
            state.errorMessage = action.error;
            state.successMessage = "";
            state.celebs = [];

        })
           
           

    }
})