import axios from 'axios';
import { UserView } from '../../../modules/users/models/UserView';

export class CelebServices {
    private static serverUrl = process.env.REACT_APP_EXPRESS_SERVER;

    private static celebsAPI: string = `${CelebServices.serverUrl}/api/celeb`

    // public
    public static getAllCeleb() {
        return axios.get(`${this.celebsAPI}/list`);
    }

    public static getAllApprovedCeleb(){
        return axios.get(`${this.celebsAPI}/list/approved`);

    }
   
}