import React, {useEffect, useState} from 'react';
import {Button, Col, Container, Form, InputGroup, Row} from "react-bootstrap";
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import {Link, useNavigate, useParams} from "react-router-dom";
import { adminFeatureKey, RootAdminState } from '../../../../redux/admin/admin.slice';
import { AppDispatch } from '../../../../redux/store';
import * as adminActions from "../../../../redux/admin/admin.actions"
import * as orderActions from "../../../../redux/orders/order.actions"
import NavBarSocial from '../../../layout/components/navbar/NavBarSocial';
import Spinner from '../../../layout/components/spinner/Spinner';
import { AddOrderView, OrderView } from '../../../order/models/OrderView';
import { RootUserState, usersFeatureKey } from '../../../../redux/users/user.slice';
import { ToastUtil } from '../../../../util/ToastUtil';
interface IProps {
}

let AddOrder: React.FC<IProps> = ({}) => {

    const adminState = useSelector((state: RootAdminState) => {
        return state[adminFeatureKey];
      })
      const navigate = useNavigate();

      const userState = useSelector((state: RootUserState) => {
        return state[usersFeatureKey]
      })
      let {user} = userState
    
      const { celebID } = useParams();
            let { loading, errorMessage, successMessage, templates } = adminState;
       const dispatch: AppDispatch = useDispatch();

       const [order, setOrder] = useState<AddOrderView>({
        orderBy: user._id,
        orderTo: celebID,
        template: "",
        need_to_complete_date: ""
    });
 

    const updateInput = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
        setOrder((prevState) => {
            return {
                ...prevState,
                [event.target.name]: event.target.value
            }
        })
    };
    const [validated, setValidated] = useState(false);

    const handleSubmit = (event: any) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === true) {
            dispatch(orderActions.addOrderAction(order)).then((response: any) => {

                if (response.error) {
                    ToastUtil.displayErrorToast(response.error.errorMessage);
                } else {
                    ToastUtil.displaySuccessToast('Order added successfully!');
                    navigate('/order/me');
                }
            })
        }
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        setValidated(true);
    };
    
      useEffect(() => {
        dispatch(adminActions.getTemplateListAction());
      }, [])
    return (
        <>

        <NavBarSocial/>
        {
            loading && <Spinner/>
        }
            <Container className="mt-3">
                <Row>
                    <Col>
                        <h3 className="text-success">
                            <i className="fa fa-black-tie"></i> Add Order
                        </h3>
                        <p className="fst-italic">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Accusantium
                            ad adipisci, assumenda atque deleniti deserunt dolorem doloribus maxime neque, odit placeat
                            quas quibusdam quidem quis quisquam quos reprehenderit similique ut!</p>
                    </Col>
                </Row>

                <Row>
                    <Col xs={4}>
                    <Form noValidate validated={validated} onSubmit={handleSubmit}>

                    <Form.Group className="mb-2">
                                <Form.Select onChange={updateInput}
                                name={'template'}
                                value={order.template} required
                                >
                                
                                    <option value="">Select Template</option>
                                    {
                                        templates.map((template, index)=>{
                                            return(
                                                <>
                                                    <option key={index} value={template._id}>{template.name}</option>
                                                </>
                                            )
                                        })
                                    }
                                </Form.Select>
                            </Form.Group>

                            <Form.Group className="mb-2">
                            <Form.Control
                                    name={'need_to_complete_date'}
                                    value={order.need_to_complete_date}
                                    onChange={updateInput}
                                    type="text" placeholder="Completion Date" required></Form.Control>
                            
                            </Form.Group>
                        <Button variant="success" type="submit" className="me-1">
                            Add Order
                        </Button>
                        <Link to={'/celeb/list'}>
                            <Button variant="dark" type="button">
                                Cancel
                            </Button>
                        </Link>
                        </Form>
 

                    </Col>
                </Row>
            </Container>
        </>
    )
};
export default AddOrder;