import { createSlice, SerializedError } from "@reduxjs/toolkit"
import { UserView } from "../../modules/users/models/UserView"
import { AuthUtil } from "../../util/AuthUtil"
import { ToastUtil } from "../../util/ToastUtil"
import * as userActions from "./user.actions"


export const usersFeatureKey = "userFeatureKey"

export interface RootUserState {
    [usersFeatureKey]: InitialState
}
export interface InitialState {
    loading: boolean,
    errorMessage: SerializedError,
    user: UserView,
    token: string | null,
    isAuthenticated: boolean,
    successMessage: string
}

const initialState: InitialState = {
    loading: false,
    errorMessage: {} as SerializedError,
    user: {} as UserView,
    token: null,
    isAuthenticated: false,
    successMessage: ""

}

export const userSlice = createSlice({
    name: "userSlice",
    initialState: initialState,
    reducers: {
        logOutAction: (state, action) => {
            ToastUtil.displaySuccessToast('LogOut is Success!');
            AuthUtil.deleteToken();
            state.user = {} as UserView;
            state.token = null;
            state.isAuthenticated = false;
    }
},
    extraReducers: (builder: any) => {
        // register a User
        builder.addCase(userActions.registerUserAction.pending, (state: any) => {
            state.loading = true;
        }).addCase(userActions.registerUserAction.fulfilled, (state: any, action: any) => {
            state.loading = false;
            state.successMessage = action.payload.msg;
        }).addCase(userActions.registerUserAction.rejected, (state: any, action: any) => {
            state.loading = false;
            state.errorMessage = action.error;
            state.successMessage = "";
        })
            // login a User
            .addCase(userActions.loginUserAction.pending, (state: any) => {
                state.loading = true;
            }).addCase(userActions.loginUserAction.fulfilled, (state: any, action: any) => {
                state.loading = false;
                state.successMessage = action.payload.msg;
                state.token = action.payload.token;
                AuthUtil.saveToken(action.payload.token); // save token to session storage
                state.isAuthenticated = true;
                console.log(action.payload.user)
                state.user = action.payload.user
            }).addCase(userActions.loginUserAction.rejected, (state: any, action: any) => {
                state.loading = false;
                state.errorMessage = action.error;
                state.successMessage = "";
                state.token = null;
                AuthUtil.deleteToken(); // delete the token
                state.isAuthenticated = false;
            })

            // get User Info
            .addCase(userActions.getUserInfoAction.pending, (state: any) => {
                state.loading = true;
            }).addCase(userActions.getUserInfoAction.fulfilled, (state: any, action: any) => {
                state.loading = false;
                state.successMessage = "";
                state.user = action.payload.user;
                state.isAuthenticated = true;
            }).addCase(userActions.getUserInfoAction.rejected, (state: any, action: any) => {
                state.loading = false;
                state.errorMessage = action.error;
                state.successMessage = "";
                state.user = {} as UserView;
                state.token = null;
                state.isAuthenticated = false;
                AuthUtil.deleteToken(); // delete the token
            })
            //update celeb status by admin
            .addCase(userActions.updateCelebStatusAction.pending, (state: any) => {
                state.loading = true;
            }).addCase(userActions.updateCelebStatusAction.fulfilled, (state: any, action: any) => {
                state.loading = false;
                state.successMessage = action.payload.msg;
                //state.user = action.payload.user;
            }).addCase(userActions.updateCelebStatusAction.rejected, (state: any, action: any) => {
                state.loading = false;
                state.errorMessage = action.error;
                state.successMessage = "";
                //state.user = {} as UserView;
            })

    }
})

export const {logOutAction} = userSlice.actions