import React, { useEffect, useState } from 'react';
import { Button, Col, Container, Form, Row, Table } from "react-bootstrap";
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { Link } from "react-router-dom";
import { RootOrderState, ordersFeatureKey } from '../../../../redux/orders/order.slice';
import { AppDispatch } from '../../../../redux/store';
import NavBarSocial from '../../../layout/components/navbar/NavBarSocial';
import Spinner from '../../../layout/components/spinner/Spinner';
import * as orderActions from '../../../../redux/orders/order.actions'
import { count } from 'console';
import { RootUserState, usersFeatureKey } from '../../../../redux/users/user.slice';

interface IProps { }

let MyOrderList: React.FC<IProps> = ({ }) => {

    const orderState = useSelector((state: RootOrderState) => {
        return state[ordersFeatureKey];
    })

    const userState = useSelector((state: RootUserState) => {
        return state[usersFeatureKey]
    })

    let { loading, errorMessage, successMessage, orders, order } = orderState;
    let { user } = userState;
    const dispatch: AppDispatch = useDispatch();

    let changeStatus = (orderId: string | undefined) => {
        if (orderId) {
            dispatch(orderActions.updateStatusAction(orderId)).then(()=>{
                dispatch(orderActions.getAllOrdersAction());
            }).catch(()=>{
                dispatch(orderActions.getAllOrdersAction());
            })

        }
    }

    useEffect(() => {
        dispatch(orderActions.getAllOrdersAction());
    }, [])
    return (
        <>
            <NavBarSocial />
            {
                loading && <Spinner />
            }

            <Container className={"mt-3"}>
                <Row>
                    <Col xs={2}>
                        <h3 className="text-success">
                            <i className="fa fa-dashboard text-success"></i> My Order</h3>
                        <p>Welcome Mr. <span className="fw-bold">{user.name}</span></p>
                    </Col>
                    {

                        !user.isCeleb && !user.isAdmin &&
                        <>
                            <Col>
                                <Link to={'/celeb/list'}>
                                    <Button variant={'success'} className="m-1">
                                        <i className="fa fa-user-plus"></i> Make New Order</Button>
                                </Link>

                            </Col>
                        </>
                    }


                </Row>

                <Row>
                    <Col>
                        <h4 className="text-success">Order List</h4>
                        <small className="fst-italic">Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                            Architecto autem nisi non perspiciatis vitae! Doloremque ea earum harum magni maiores minima
                            non quia recusandae repellendus! Id itaque quam veritatis. Nostrum?</small>
                        <Table bordered striped className="text-center shadow-lg">
                            <thead>
                                <tr>
                                    {
                                        user.isAdmin &&
                                        <>
                                            <th>Sr No.</th>
                                            <th>Order By</th>
                                            <th>Order To</th>
                                            <th>Occasion</th>
                                            <th>Completion Date</th>
                                            <th>Status</th>
                                            <th>Actions</th>
                                        </>

                                    }
                                    {
                                        user.isCeleb &&
                                        <>
                                            <th>Sr No.</th>
                                            <th>Order By</th>
                                            <th>Occasion</th>
                                            <th>Completion Date</th>
                                            <th>Status</th>
                                            <th>Actions</th>
                                        </>

                                    }
                                    {
                                        !user.isCeleb && !user.isAdmin &&
                                        <>
                                            <th>Sr No.</th>
                                            <th>Order To</th>
                                            <th>Occasion</th>
                                            <th>Completion Date</th>
                                            <th>Status</th>
                                            <th>Actions</th>
                                        </>

                                    }

                                </tr>
                            </thead>
                            <tbody>
                                {

                                    orders.map((orderRow, index) => {
                                        return (
                                            <tr key={orderRow._id}>
                                                <td>{index + 1}</td>

                                                {
                                                    user.isAdmin &&
                                                    <>
                                                        <td>{orderRow.orderBy?.name}</td>
                                                        <td>{orderRow.orderTo?.name}</td>
                                                        <td>{orderRow.template?.name}</td>
                                                        <td>{orderRow.need_to_complete_date}</td>
                                                        <td>{orderRow.status == 'new_order' ? "New Order" : (orderRow.status == 'uploaded' ? "Uploaded" : "Completed")}</td>
                                                    </>

                                                }
                                                {
                                                    user.isCeleb &&
                                                    <>
                                                        <td>{orderRow.orderBy?.name}</td>
                                                        <td>{orderRow.template?.name}</td>
                                                        <td>{`${orderRow.need_to_complete_date}`}</td>
                                                        <td>{orderRow.status == 'new_order' ? "New Order" : (orderRow.status == 'uploaded' ? "Uploaded" : "Completed")}</td>


                                                    </>

                                                }
                                                {
                                                    !user.isCeleb && !user.isAdmin &&
                                                    <>
                                                        <td>{orderRow.orderTo?.name}</td>
                                                        <td>{orderRow.template?.name}</td>
                                                        <td>{orderRow.need_to_complete_date}</td>
                                                        <td>{orderRow.status == 'new_order' ? "New Order" : (orderRow.status == 'uploaded' ? "Uploaded" : "Completed")}</td>

                                                    </>

                                                }


                                                <td>
                                                    {
                                                        user.isCeleb && orderRow.status == 'new_order' &&
                                                        <>
                                                            <Link to={`/order/upload/${orderRow._id}`}>
                                                                <Button variant="primary" className="me-2">
                                                                    <i className="fa fa-upload"></i>
                                                                </Button>
                                                            </Link>
                                                        </>

                                                    }
                                                    {
                                                        !user.isCeleb && !user.isAdmin &&
                                                        <>
                                                            <Link to={`/order/detail/${orderRow._id}`}>
                                                                <Button variant="primary" className="me-2">
                                                                    <i className="fa fa-eye"></i>
                                                                </Button>


                                                            </Link>
                                                        </>

                                                    }
                                                    {
                                                        user.isAdmin &&
                                                        <>
                                                            <Link to={`/order/detail/${orderRow._id}`}>
                                                                <Button variant="primary" className="me-2">
                                                                    <i className="fa fa-eye"></i>
                                                                </Button>


                                                            </Link>
                                                            {
                                                                orderRow.status === 'uploaded' && orderRow._id &&
                                                                <>

                                                                    <Button onClick={() => changeStatus(orderRow._id)} variant="primary" className="me-2">
                                                                        <i className="fa fa-edit"></i>
                                                                    </Button>

                                                                </>
                                                            }
                                                            <Button variant="danger">
                                                                <i className="fa fa-trash"></i>
                                                            </Button>

                                                        </>

                                                    }


                                                </td>
                                            </tr>
                                        )
                                    })
                                }


                            </tbody>
                        </Table>
                    </Col>
                    <hr />
                </Row>


            </Container>
        </>
    )
};
export default MyOrderList;