import React, { useEffect, useState } from 'react';
import { Button, Col, Container, Form, Row, Table } from "react-bootstrap";
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { Link } from "react-router-dom";
// import { AppDispatch } from '../../../../redux/store';
// import NavBarSocial from '../../../layout/components/navbar/NavBarSocial';
// import Spinner from '../../../layout/components/spinner/Spinner';
import * as adminActions from '../../../redux/admin/admin.actions'
import { count } from 'console';
import { RootUserState, usersFeatureKey } from '../../../redux/users/user.slice';
import { adminFeatureKey, RootAdminState } from '../../../redux/admin/admin.slice';
import { AppDispatch } from '../../../redux/store';
import NavBarSocial from '../../layout/components/navbar/NavBarSocial';
import Spinner from '../../layout/components/spinner/Spinner';

interface IProps { }

let CategoryList: React.FC<IProps> = ({ }) => {

    const adminState = useSelector((state: RootAdminState) => {
        return state[adminFeatureKey];
    })

    const userState = useSelector((state: RootUserState) => {
        return state[usersFeatureKey]
    })

    let { loading, errorMessage, successMessage, categories } = adminState;
    let { user } = userState;
    const dispatch: AppDispatch = useDispatch();

     


    useEffect(() => {
        dispatch(adminActions.getCategoryListAction());
    }, [])
    return (
        <>
            <NavBarSocial />
            {
                loading && <Spinner />
            }

            <Container className={"mt-3"}>
                <Row>

                    {

                        user.isAdmin &&
                        <>
                            <Col>
                                <Link to={'/categories/add'}>
                                    <Button variant={'success'} className="m-1">
                                        <i className="fa fa-user-plus"></i> Add New Category</Button>
                                </Link>

                            </Col>
                        </>
                    }


                </Row>

                <Row>
                    <Col>
                        <h4 className="text-success">Category</h4>
                        <small className="fst-italic">Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                            Architecto autem nisi non perspiciatis vitae! Doloremque ea earum harum magni maiores minima
                            non quia recusandae repellendus! Id itaque quam veritatis. Nostrum?</small>
                        <Table bordered striped className="text-center shadow-lg">
                            <thead>
                                <tr>
                                    {
                                        user.isAdmin &&
                                        <>
                                            <th>Sr No.</th>
                                            <th>Name</th>
                                            <th>Description</th>
                                            <th>Actions</th>
                                        </>
                                    }


                                </tr>
                            </thead>
                            <tbody>
                                {

                                    categories.map((category, index) => {
                                        return (
                                            <tr key={category._id}>
                                                <td>{index + 1}</td>

                                                {
                                                    user.isAdmin &&
                                                    <>
                                                        <td>{category.name}</td>
                                                        <td>{category.description}</td>

                                                    </>

                                                }
                                                <td>
                                                <Link to={`/categories/edit/${category._id}`}>
                                                    <Button  variant="primary" className="me-2">
                                                        <i className="fa fa-edit"></i>
                                                    </Button>
                                                    </Link>
                                                </td>
                                            </tr>
                                        )
                                    })
                                }


                            </tbody>
                        </Table>
                    </Col>
                    <hr />
                </Row>


            </Container>
        </>
    )
};
export default CategoryList;