import React, { useEffect } from 'react';
import { Button, Col, Container, Form, Row } from "react-bootstrap";
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { Link, useParams } from "react-router-dom";
import { ordersFeatureKey, RootOrderState } from '../../../../redux/orders/order.slice';
import { AppDispatch } from '../../../../redux/store';
import { RootUserState, usersFeatureKey } from '../../../../redux/users/user.slice';
import * as orderActions from '../../../../redux/orders/order.actions'
import NavBarSocial from '../../../layout/components/navbar/NavBarSocial';
import Spinner from '../../../layout/components/spinner/Spinner';

interface IProps { }

let OrderDetail: React.FC<IProps> = ({ }) => {

  const orderState = useSelector((state: RootOrderState) => {
    return state[ordersFeatureKey];
  })

  let { orderId } = useParams()
  const userState = useSelector((state: RootUserState) => {
    return state[usersFeatureKey]
  })

  let { loading, errorMessage, successMessage, order } = orderState;
  let { user } = userState;
  const dispatch: AppDispatch = useDispatch();

  useEffect(() => {
    console.log("useEffect")
    console.log(`order_id:  ${orderId}`)

    if (orderId) {
      dispatch(orderActions.getOrderAction(orderId));
    }
  }, [])

  return (
    <>
      <NavBarSocial />
      {
        loading && <Spinner />
      }
      <Container className={"mt-3"}>

        <Row>
          <Col>
            <h3 className="text-success">
              <i className="fa fa-black-tie"></i> Order Details
            </h3>
            <p className="fst-italic">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Accusantium
              ad adipisci, assumenda atque deleniti deserunt dolorem doloribus maxime neque, odit placeat
              quas quibusdam quidem quis quisquam quos reprehenderit similique ut!</p>
          </Col>
        </Row>

        <table>
          <tr>
            <td>Order ID</td>
            <td>{order._id}</td>
          </tr>
          <tr>
            <td>Order By</td>
            <td>{order.orderBy?.name}</td>
          </tr>
          <tr>
            <td>Order To</td>
            <td>{order.orderTo?.name}</td>
          </tr>
          <tr>
            <td>Order Status</td>
            <td>{order.status}</td>
          </tr>
          <tr>
            <td className='m-3'>Completion date</td>
            <td>{order.need_to_complete_date}</td>
          </tr>
          {
            order.status === "completed" &&
            <>
              <tr>
                <td className='m-3'>Attachment</td>
                <td><img src={order.attachment} width='200px' height='200px' /></td>
              </tr>
            </>
          }

          <tr>
            <td ><Link to={'/order/me'}>
                            <Button variant="dark" type="button">
                                Back
                            </Button>
                        </Link></td>
            <td></td>
          </tr>
        </table>

      </Container>

    </>
  )
};
export default OrderDetail;