import { combineReducers } from "@reduxjs/toolkit";
import * as userReducer from './users/user.slice';
import * as orderReducer from './orders/order.slice';
import * as celebReducer from './celeb/celeb.slice';
import * as adminReducer from './admin/admin.slice';



const rootReducer = combineReducers({
    [userReducer.usersFeatureKey] : userReducer.userSlice.reducer,
    [orderReducer.ordersFeatureKey] : orderReducer.orderSlice.reducer,
    [celebReducer.celebsFeatureKey] : celebReducer.celebSlice.reducer,
    [adminReducer.adminFeatureKey] : adminReducer.adminSlice.reducer
})

export default rootReducer;