import React from 'react';
import { Button, Card, Col, Container, Form, InputGroup, Row, Table } from "react-bootstrap";
import { useDispatch } from 'react-redux';
import { Link } from "react-router-dom";
import { AppDispatch } from '../../../../redux/store';
import { UserView } from '../../models/UserView';
import constants from '../../../../constants.json';


interface IProps {
  celeb: UserView
  isUser: boolean
  isAdmin?: boolean
  // setClicked:()=>void
  setApprove: (celebId: string) => void;

}


let CelebCard: React.FC<IProps> = (props: IProps) => {
  const dispatch: AppDispatch = useDispatch();

  let { celeb, isUser, isAdmin,setApprove } = props

  
  let changeCelebStatus = (celebId: string | undefined) => {
    if (celebId) {
      setApprove(celebId);
    }
    
  }
  return (
    <>
      <Card style={{ width: '18rem' }}>
        <Card.Img variant="top" src={celeb.imageUrl} height='250px' />
        <Card.Body>
          <Card.Title>{celeb.name}</Card.Title>
          <Card.Text>
            {celeb.description}
          </Card.Text>
          <Card.Text className='text-success fw-bold'> &#8377;
            {celeb.price}
          </Card.Text>

          {
            isUser &&
            <>
              <Link to={`/order/add/${celeb._id}`}>
                <Button variant={'success'} className="me-1">Select</Button>
              </Link>
            </>
          }
          {
            isAdmin && celeb.status &&
            <>
              {
                celeb.status == constants.CELEB_STATUS.NEW &&
                <>
                  <Button onClick={()=>changeCelebStatus(celeb._id)} variant={'success'} className="me-1">Approve</Button>
                </>
              }

            </>
          }
          <Link to={'/order/me'}>
            <Button variant="dark" type="button">
              Cancel
            </Button>
          </Link>
        </Card.Body>
      </Card>
    </>
  )
};
export default CelebCard;